@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,400;1,300&display=swap);
body {
  margin: 0;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
} */

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.App {
  background-color: #282c34;
}

.header-main-container {
  min-height: 60vh;
  display: flex;
  justify-content: space-between;
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 15% 15% 0 15%;
}
.contact {
  margin-top: 10% !important;
  font-weight: normal;
  font-size: calc(10px + 1vmin);
}
.contact a {
  text-decoration: none;
  color: white;
  border-bottom: 1px solid #34475f;
}

.contact a:hover {
  color: #34475f;
}
.header-content {
  max-width: 700px;
  font-weight: 350;
}

/*span wave component*/

span.wave {
  -webkit-animation-name: wave-animation;
          animation-name: wave-animation;
  -webkit-animation-duration: 2.5s;
          animation-duration: 2.5s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  transform-origin: 70% 70%;
  display: inline-block;
}

@-webkit-keyframes wave-animation {
  0% {
    transform: rotate(0deg);
  }
  10% {
    transform: rotate(-10deg);
  }
  20% {
    transform: rotate(12deg);
  }
  30% {
    transform: rotate(-10deg);
  }
  40% {
    transform: rotate(9deg);
  }
  50% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

@keyframes wave-animation {
  0% {
    transform: rotate(0deg);
  }
  10% {
    transform: rotate(-10deg);
  }
  20% {
    transform: rotate(12deg);
  }
  30% {
    transform: rotate(-10deg);
  }
  40% {
    transform: rotate(9deg);
  }
  50% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

.intro-line1 {
  margin-top: 10%;
}

.intro-line1 .bold {
  font-weight: 500;
  margin: 0 10px;
  font-size: calc(12px + 3vmin);
}

.header-image {
  max-width: 400px;
}

/* On screens that are 992px or less,*/
@media screen and (max-width: 992px) {
  .header-main-container {
    display: block;
    padding-right: 10%;
  }
  .header-image-container {
    display: flex;
    justify-content: center;
    margin-top: 22%;
  }

  .header-image {
    width: 80%;
  }
}

.background-main-container {
  color: white;
  padding: 12% 15% 5% 15%;
}

.background-header {
  font-size: calc(12px + 2vmin);
}

.background-content-content {
  margin-left: 15%;
  font-size: calc(12px + 1vmin);
  margin-bottom: 5%;
  font-weight: lighter;
}

.background-content-content a {
  font-weight: bold;
  text-decoration: none;
  color: white;
}

@media screen and (max-width: 992px) {
  .background-content-content {
    margin-left: 0px;
  }
}

.skills-main-container {
  color: white;
  padding: 5% 15% 5% 15%;
}

.skills-header {
  font-size: calc(12px + 2vmin);
  font-weight: 400;
  margin-bottom: 5%;
}

.skills-content-container {
  margin-left: 15%;
}
.each-skills {
  width: 200px;
  margin-bottom: 5%;
}

ul {
  list-style-type: none;
}

ul,
li {
  margin: 0;
  padding: 0;
  margin-bottom: 1%;
}

.skills-content-container {
  max-width: 1335px;
}

.grid-row {
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
}

.each-skills {
  flex-basis: 20%;
  -ms-flex: auto;
  font-weight: lighter;
}

@media screen and (max-width: 992px) {
  .skills-content-container {
    margin-left: 0px;
  }
  .skills-main-container {
    padding-right: 10%;
  }
}

@media (max-width: 1333px) {
  .each-skills {
    flex-basis: 33.33%;
  }
}
@media (max-width: 1073px) {
  .each-skills {
    flex-basis: 33.33%;
  }
}
@media (max-width: 815px) {
  .each-skills {
    flex-basis: 50%;
  }
}

.footer-container {
  min-height: 50px;
  display: flex;
  justify-content: center;
  color: white;
}

.footer-container {
  padding-top: 2%;
  padding-bottom: 2%;
}

.heart {
  font-size: calc(9px + 3vmin);

  margin: 0 10px 0 10px;
}
.animate-beat {
  -webkit-animation: beat 1s infinite;
          animation: beat 1s infinite;
}

@-webkit-keyframes beat {
  0% {
    font-size: calc(7px + 1vmin);
  }
  40% {
    font-size: calc(9px + 1vmin);
  }
  60% {
    font-size: calc(7px + 1vmin);
  }
  80% {
    font-size: calc(9px + 1vmin);
  }
  100% {
    font-size: calc(7px + 1vmin);
  }
}

@keyframes beat {
  0% {
    font-size: calc(7px + 1vmin);
  }
  40% {
    font-size: calc(9px + 1vmin);
  }
  60% {
    font-size: calc(7px + 1vmin);
  }
  80% {
    font-size: calc(9px + 1vmin);
  }
  100% {
    font-size: calc(7px + 1vmin);
  }
}

@media screen and (max-width: 992px) {
  .footer-content {
    max-width: 250px;
    text-align: center;
    padding-top: 10%;
    padding-bottom: 10%;
  }
}

